
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "shippingApplication",
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
Cell,
Group,
XInput,
XButton
},
created() {
this.queryHistoryShipOrder();
this.queryShipOrder();
},
computed: {
},
data() {
return {
index: 0,
checkerWhich: 2,
userInfo: {},
orderList: [],
shipOrderList: []
};
},
methods: {
queryHistoryShipOrder() {
this.$vux.loading.show({
text: ''
})
this.$api
.queryHistoryShipOrder({
pageNo: 1,
pageSize: 100
})
.then(data => {
if (data.code === 200) {
this.orderList = data.data.items;
}
this.$vux.loading.hide()
});
},
queryShipOrder() {
this.$vux.loading.show({
text: ''
})
this.$api
.queryShipOrder({
pageNo: 1,
pageSize: 1000
})
.then(data => {
if (data.code === 200) {
this.shipOrderList = data.data.items;
}
this.$vux.loading.hide()
});
},
shipOrder(id, isSelf) {
this.$api
.shipOrder({
orderId: id,
isSelf: isSelf
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("操作成功", "top");
this.queryShipOrder();
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
toSale() {
this.$router.push({
name: "sales"
});
},
selectChecker(val) {
if (val === 0) {
this.queryShipOrder();
} else if (val === 1) {
this.queryHistoryShipOrder();
}
},
checkOrder(order) {
if (order.orderStatus === 0) {
return '未支付';
} else if (order.orderStatus === 1) {
return '待发货';
} else {
if (order.isCompanyDelivery === 1) {
return '公司发货';
} else {
let uid = localStorage.getItem('uid');
if (order.trueShipUid == uid) {
return '已发货';
} else {
return '上级发货';
}
}
}
}
}
};



